
import Swiper from 'vux/src/components/swiper/swiper.vue'






















export default {
components: {
Swiper,
},
ready() {},
data() {
return {
goods_info: {
goods_id: "",
goods_title: "",
goods_money: 0,
goods_stock: 0,
spec: {
"map": [],
paramList: []
},
img_list: [],
is_collection: false
},
swiperList: [],
}
},
mounted() {
this.update();
},
methods: {
update() {

this.$get(
"carousel/getList", {},
res => {
if(res.res >= 1) {
this.swiperList = res.msg;
for(var i = 0; i < this.swiperList.length; i++) {
this.swiperList[i].img = this.$getUrl(this.swiperList[i].src);
}
}
if(res < 0) {}
}
);

}
},
}



